'use client';

import { Button } from "@heroui/react";
import { useParams } from "next/navigation";
import Link from "next/link";
import { useUserStore } from "../../../stores/user/userStore";

export default function UserDetailPage() {
    const params = useParams();
    const userId = params.id as string;

    const { users, deleteUser } = useUserStore();
    const user = users.find(u => u.id === userId);

    const handleDelete = () => {
        if (confirm(`Are you sure you want to delete user "${user?.name}"?`)) {
            deleteUser(userId);
            window.location.href = '/user';
        }
    };

    if (!user) {
        return (
            <div className="min-h-screen bg-black text-white p-6">
                <div className="text-center">
                    <h1 className="text-2xl font-bold mb-4">User Not Found</h1>
                    <Link href="/user">
                        <Button>
                            Back to Users
                        </Button>
                    </Link>
                </div>
            </div>
        );
    }

    const getRoleColor = (role: string) => {
        switch (role) {
            case 'Administrator': return 'bg-purple-600/20 text-purple-400 border-purple-600/30';
            case 'Field Supervisor': return 'bg-orange-600/20 text-orange-400 border-orange-600/30';
            case 'Engineer': return 'bg-blue-600/20 text-blue-400 border-blue-600/30';
            default: return 'bg-gray-600/20 text-gray-400 border-gray-600/30';
        }
    };

    return (
        <div className="min-h-screen bg-black text-white p-6">
            {/* Header */}
            <div className="flex items-center justify-between mb-8">
                <div>
                    <h1 className="text-2xl font-bold mb-1">User Details</h1>
                    <p className="text-gray-400 text-sm">View user information and permissions</p>
                </div>
                <Link href="/user">
                    <Button className="border-zinc-700">
                        ‚Üê Back to Users
                    </Button>
                </Link>
            </div>

            {/* User Info Card */}
            <div className="bg-zinc-900 rounded-lg border border-zinc-800 p-6 max-w-3xl">
                <div className="space-y-6">
                    {/* ID */}
                    <div>
                        <label className="text-sm text-gray-400 block mb-2">User ID</label>
                        <p className="text-white font-medium">{user.id}</p>
                    </div>

                    {/* Name */}
                    <div>
                        <label className="text-sm text-gray-400 block mb-2">Full Name</label>
                        <p className="text-white font-medium text-lg">{user.name}</p>
                    </div>

                    {/* Email */}
                    <div>
                        <label className="text-sm text-gray-400 block mb-2">Email Address</label>
                        <p className="text-white font-medium">{user.email}</p>
                    </div>

                    {/* Role */}
                    <div>
                        <label className="text-sm text-gray-400 block mb-2">Role</label><br></br>
                        <span className={`inline-flex items-center px-3 py-1.5 rounded text-sm font-medium border ${getRoleColor(user.role)}`}>
                            {user.role}
                        </span>
                    </div>

                    {/* Status */}
                    <div>
                        <label className="text-sm text-gray-400 block mb-2">Status</label><br></br>
                        {user.status === 'Active' ? (
                            <span className="inline-flex items-center gap-2 text-green-400">
                                <span className="w-2 h-2 rounded-full bg-green-400"></span>
                                Active
                            </span>
                        ) : (
                            <span className="inline-flex items-center gap-2 px-3 py-1 rounded text-sm font-medium bg-gray-600/20 text-gray-400 border border-gray-600/30">
                                Inactive
                            </span>
                        )}
                    </div>

                    {/* Created Date */}
                    <div>
                        <label className="text-sm text-gray-400 block mb-2">Created Date</label>
                        <p className="text-white">{user.createdDate}</p>
                    </div>
                </div>

                {/* Actions */}
                <div className="flex gap-3 mt-8 pt-6 border-t border-zinc-800">
                    <Link href={`/user/${userId}/edit`}>
                        <Button className="bg-blue-600 hover:bg-blue-700 text-white">
                            Edit User
                        </Button>
                    </Link>
                    <Button
                        onClick={handleDelete}
                        className="bg-red-600 hover:bg-red-700 text-white"
                    >
                        Delete User
                    </Button>
                    <Button
                        className="border-zinc-700"
                    >
                        Reset Password
                    </Button>
                </div>
            </div>
        </div>
    );
}